
<template>
  <div>
    <section id="main-content" class="main-content">
        <loader :is-visible="isLoading"></loader>
    <div class="col-md-12 p-0">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('ManageCommunicationModes')}}</span>
                    </div>
                </div>
        <Form ref="form" v-slot="{ valid,errors}">
        <div class="col-md-12 p-0">
            <div class="border p-3">
                <div class="row">
                    <div class="col-lg-6">
                        <div class="form-group">                          
                            <span>Type Name:</span><span class="text-danger">*</span>
                                <input class="form-control" type="hidden" id="modeTypeId" name="modeTypeId" />
                                  <Field name="CommunicationTypeName" :rules="{required:true}" label="Type Name" v-slot="{errors}">
                                    <input  v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }" type="text"  maxlength="200" v-model="CommunicationTypeName"   />                                        
                                    <span v-if="errors[0]"  name="CommunicationTypeName" class="invalid-feedback">{{errors[0]}}</span>                                
                                 </Field>     
                        </div>
                    </div>
                    <div class="col-lg-6">
                        <div class="form-group">
                            <span>Status:</span>
                            <Field :rules="{'required':false}" name="status">
                                <select   class="form-control valid"  v-model="statuS_ID" >
                                    <option v-for="(item,index) in DDLList" v-bind:value="item.statuS_ID"  :key="index">
                                        {{item.status}}
                                    </option>
                                </select>
                            </Field>
                            
                        </div>
                    </div>
                    <div class="col-lg-12">
                        <div class="form-group">
                            <span>Description:</span>
                            <Field :rules="{'required':false}" name="CommunicationTypeDescriptionId">
                                <textarea id="CommunicationTypeDescriptionId" name="CommunicationTypeDescriptionId" class="form-control"
                                    style="resize:none" cols="10" rows="5" v-model="CommunicationTypeDescription">
                                </textarea>
                            </Field>
                            
                        </div>
                    </div>

                    <div class="clearfix"></div>
                    <div class="col-lg-12 mb-2">

                        <div class="row flex-row-reverse">

                            <div class="col-lg-6 text-right">
                                <a href="javascript:;" v-on:click="Save(valid,errors)" title="Save"
                                    class="btn btn-success save-btn mr-1">
                                    <em class="fa fa-save pr-2"></em>
                                    {{$t('Save')}}
                                </a>
                                <a asp-controller="CommunicationModes" asp-action="Index" title="Cancel"
                                    class="submitbtn btn btn-danger" @click="onCancel"><em    class="fa fa-close pr-2"></em>{{$t('Cancel')}}</a>
                            </div>


                            <div class="col-lg-6">
                                <div class="py-2"><small class="text-danger">{{$t('MandatoryString')}}</small>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>

            </div>
        </div>
        </Form>
    </section>
</div>
</template>
<script>
import CommunicationModesDataService from "../../../services/DataService";
export default {
                                               
    
    data() {
        return {
            language: '',
            DDLList: [],           
            CommunicationTypeName:"",
            CommunicationTypeDescription:"",
            statuS_ID: 1001,
            txtCommunicationMode: '',
            txtCommunicationModeID: 0,
            id: this.$route.params.id,
            modeTypeId:0,
            isLoading: false,
            
        }   
    },
    created: function () {

        this.GetStatusList("Views", "en");
        if (this.$route.params.id != 0) {
                this.EditCommunicationModeType(this.$route.params.id);
            }
            
    },   
    methods: {        
        onCancel:function(){
        this.$router.push({name:'CommunicationmodeList'}); 
    },
        goHome: function(){
         this.$router.push('/CommunicationModes/Index'); 
      },
        GetStatusList: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
            CommunicationModesDataService.GetStatusList(url).then(function (response) {
                var json = response.data;
                vm.DDLList = json;
            });
        },
        EditCommunicationModeType: function (id) {
                var vm=this;   
                CommunicationModesDataService.GetCommunicationModeData(id).then(response => {
                    if (response.data != null) {
                        vm.modeTypeId = response.data[0].modeTypeId;
                        vm.CommunicationTypeDescription = response.data[0].communicationTypeDescription;
                        vm.CommunicationTypeName = response.data[0].communicationTypeName;
                        vm.statuS_ID = response.data[0].statusId;
                        var frmFields = {}
                        frmFields['CommunicationTypeName'] = vm.CommunicationTypeName;
                        frmFields['CommunicationTypeDescriptionId'] = vm.CommunicationTypeDescription;
                        frmFields['status'] = vm.statuS_ID;
                        // vm.$refs.form.resetForm({ values: frmFields });
                    }
                });
            },
        Save(valid,errors){
            var vm = this;
            //  vm.$refs.form.setFieldTouched('CommunicationTypeName', true);
             vm.$refs.form.validate().then((result) => {
         vm.vSelectValidate = true;                
                    if (result) {            
                        var jsonobj = {
                            CommunicationTypeName: vm.CommunicationTypeName,
                            CommunicationTypeDescription: vm.CommunicationTypeDescription,
                            StatusId: vm.statuS_ID,
                            ModeTypeId: vm.modeTypeId,
                        };
            var box='';
            if (jsonobj.CommunicationTypeName !="")
            {
            var jobj = JSON.stringify(jsonobj);
            CommunicationModesDataService.CommunicationModesAdd(jobj).then((response) => { 
                
                if (response.data =='0') {
                    box = vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t('CommunicationModes')]), "success", true, vm.$t("Alert"));
                     vm.goHome();
                }
                else if(response.data =='1'){
                    box = vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t('CommunicationModes')]), "success", true, vm.$t("Alert"));
                      
                    vm.goHome();
                }
                else if(response.data =='2'){                 
                    box = vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t('CommunicationModes')]), "warning", true, vm.$t("Alert"));
                }
                else {
                    box = vm.ShowAlert(vm.$t('AddedError', [vm.$t("CommunicationModes")]), "failure", true, vm.$t("Alert"));
                }
            })
        }
        else{
            return -1;
        }
    }else{
        console.log(this.$refs.form.errors);
    }                       
    })
           
            
        }
    }
}
</script>